import React from 'react';
import { withRouter } from "react-router-dom";
import AppBar from 'material-ui/AppBar';
import Tabs, { Tab } from 'material-ui/Tabs';

class Navbar extends React.Component {

    handleChange = (event, value) => {
        this.props.history.push(value);
    };

    render() {
        return (
            <AppBar position="default" color="default">
                <Tabs
                  value={this.props.history.location.pathname}
                  onChange={this.handleChange}
                  indicatorColor="primary"
                  textColor="primary"
                  fullWidth
                >
                  <Tab value="/csg/" label="Home" />
                  <Tab value="/csg/faculty" label="Faculty" />
                  <Tab value="/csg/students" label="Students" />
                  <Tab value="/csg/projects" label="Projects" />
                  <Tab value="/csg/events" label="Events" />
                  <Tab value="/csg/publications" label="Publications" />
                </Tabs>
            </AppBar>
        );
    }
}

export default withRouter(Navbar);



